<template>
  <div id="container">
    <div class="wrapper">
      <div class="box header">Header</div>
      <div class="box sidebar">Sidebar</div>
      <div class="box sidebar2">Sidebar 2</div>
      <div class="box content">Content
        <br /> More content than we had before so this column is now quite tall.
        <p>{{ msg }}</p>
      </div>
      <div class="box footer">Footer</div>
    </div>
  </div>
</template>



<script>
  export default {
    name: 'Grid',
    data () {
      return {
        msg: 'Welcome to the GRID'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  body {
    margin: 40px;
  }

  .sidebar {
          grid-area: sidebar;
      }

      .sidebar2 {
          grid-area: sidebar2;
      }

      .content {
          grid-area: content;
      }

      .header {
          grid-area: header;
      }

      .footer {
          grid-area: footer;
      }

      .wrapper {
          background-color: #fff;
          color: #444;
      }

    .wrapper {
      display: grid;
      grid-gap: 1em;
      grid-template-areas:
       "header"
       "sidebar"
       "content"
       "sidebar2"
       "footer"
    }

      @media only screen and (min-width: 500px)  {
      .wrapper {

          grid-template-columns: 20% auto;
          grid-template-areas:
      "header   header"
          "sidebar  content"
          "sidebar2 sidebar2"
          "footer   footer";
      }
      }

      @media only screen and (min-width: 600px)   {
          .wrapper {
        grid-gap: 20px;
              grid-template-columns: 120px auto 120px;
              grid-template-areas:
        "header  header  header"
              "sidebar content sidebar2"
              "footer  footer  footer";
          }
      }

  .box {
    background-color: #444;
    color: #fff;
    border-radius: 5px;
    padding: 10px;
    font-size: 150%;
  }

  .header,
  .footer {
    background-color: #999;
    left:0px;
    bottom:0px;
  }

  .sidebar2 {
    background-color: #ccc;
    color: #444;
  }
</style>



